.profile {
    flex: 1;
}

.profile-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.edit {
    
    background: #dde6ed;
    padding: 12px;
    border-radius: 10px;
    color: #969393;
    display: flex;
    align-items: center;
    justify-content: center;
}

.edit .icon {
    font-size: 20px;
    transition: 0.1s ease-in-out;
}

.edit .icon:hover {
    transform: scale(1.3);
}


.user-profile {
    background: #dde6ed;
    margin-top: 2rem;
    height: 85%;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}

.profession {
    color: #969393;
}
.user-details {
    width: 100%;
    height: 250px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.user-details img{
    background: white;
    width: 150px;
    border-radius: 50%;
    margin-bottom: 10px;
}

.user-details .user-name {
    color: #525d82;
}


.user-courses {
    background: white;
    flex: 1;
    margin: 10px;
    border-radius: 10px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.course{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #dde6ed;
    padding: 10px;
    border-radius: 10px;
}
.course-detail{
    display: flex;
    align-items: center;
    gap: 20px;
}
.course-cover {
    background-color: white;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 20px;
}

.course .action {
    font-weight: 900;
    font-size: 1.2rem;

}